<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>
        <a href="/">用户管理</a>
      </el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!-- 搜索 -->
      <el-row :gutter="20">
        <el-col :span="7">
          <el-input
            placeholder="请输入用户名"
            class="input-with-select"
            v-model="uservo.jxyxUser.username"
            clearable
            @clear="queryPage"
            @keyup.enter.native="queryPage"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="queryPage"
            ></el-button>
          </el-input>
        </el-col>

        <el-row :gutter="10" class="el-row">
          <el-col :span="1.5">
            <el-button
              type="primary"
              icon="el-icon-plus"
              v-if="is_authority.is_user_add"
              @click="(dialogAddUser = true)"
              >新增</el-button
            >
          </el-col>
          
        </el-row>

      </el-row>
      <!-- 数据 -->
      <el-table
        :data="userList"
        border
        stripe
        @selection-change="handleSelectionChange"
      >
        <el-table-column
          type="index"
          width="80"
          align="center"
          label="用户编号"
        ></el-table-column>
        <el-table-column
          prop="username"
          label="用户名"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="roleName"
          label="角色"
          align="center"
        ></el-table-column>
        <el-table-column label="状态" align="center">
          <!-- 作用域插槽 -->
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.mgState"
              active-color="#13ce66"
              inactive-color="#ff4949"
              @change="uptStatus(scope.row)"
            ></el-switch>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="180px" align="center">
          <template slot-scope="scope">
            <!-- 修改 -->
            <el-tooltip
              class="item"
              effect="dark"
              content="编辑"
              v-if="is_authority.is_user_upt"
              placement="top-start"
              :enterable="false"
            >
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="editUser(scope.row)"
              ></el-button>
            </el-tooltip>
            <!-- 删除 -->
            <el-tooltip
              class="item"
              effect="dark"
              content="删除"
              placement="top-start"
              :enterable="false"
              v-if="is_authority.is_user_del"
            >
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="del(scope.row)"
              ></el-button>
            </el-tooltip>

            <!-- 分配角色 -->
            <el-tooltip
              class="item"
              effect="dark"
              content="分配角色"
              placement="top-start"
              :enterable="false"
              v-if="is_authority.is_user_add_role"
            >
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="fprole(scope.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page.current"
        :page-sizes="[5, 10, 20, 30, 40]"
        :page-size="page.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page.total"
      ></el-pagination>
      <!-- 对话框 :before-close="handleClose" 在对话框关闭之前执行-->
      <!-- 标题 -->
      <!-- 编辑用户名 -->
      <el-dialog
        title="编辑用户"
        :visible.sync="dialogAddUser"
        width="50%"
        @close="closeInit"
      >
        <!-- 内容主体 -->

        <el-form
          :model="addUserForm"
          :rules="addUserRules"
          ref="addFormRef"
          label-width="70px"
          class="demo-ruleForm"
        >
          <el-form-item label="用户名" prop="username">
            <el-input v-model="addUserForm.username"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password" v-if="passEditUser">
            <el-input v-model="addUserForm.password"></el-input>
          </el-form-item>
        </el-form>
        <!-- 按钮 -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogAddUser = false">取 消</el-button>
          <el-button type="primary" @click="addUser">确 定</el-button>
        </span>
      </el-dialog>

      <el-dialog
        title="分配角色"
        :visible.sync="dialogAddRole"
        width="20%"
        @close="closeAddRole"
      >
        <el-form ref="form" v-model="addUserForm" label-width="80px">
          <el-form-item label="选择角色">
            <el-select v-model="addUserForm.roleId" placeholder="请选择角色">
              <el-option
                :label="item.roleName"
                :value="item.id"
                v-for="(item, index) in role_list"
                :key="index"
              ></el-option>
            </el-select>
          </el-form-item>

          <el-button @click="addRole" type="primary" round>确定</el-button>
          <el-button round @click="closeAddRole">取消</el-button>
        </el-form>
      </el-dialog>
    </el-card>

    <!-- 用户导入对话框 -->
    <!-- <el-dialog :title="upload.title" :visible.sync="upload.open" width="400px" append-to-body>
      <el-upload
        ref="upload"
        :limit="1"
        accept=".xlsx, .xls"
        :headers="upload.headers"
        :action="upload.url + '?updateSupport=' + upload.updateSupport"
        :disabled="upload.isUploading"
        :on-progress="handleFileUploadProgress"
        :on-success="handleFileSuccess"
        :auto-upload="false"
        drag
      >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">
          将文件拖到此处，或
          <em>点击上传</em>
        </div>
        <div class="el-upload__tip" slot="tip">
          <el-checkbox v-model="upload.updateSupport" />是否更新已经存在的用户数据
          <el-link type="info" style="font-size:12px" @click="importTemplate">下载模板</el-link>
        </div>
        <div class="el-upload__tip" style="color:red" slot="tip">提示：仅允许导入“xls”或“xlsx”格式文件！</div>
      </el-upload>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitFileForm">确 定</el-button>
        <el-button @click="upload.open = false">取 消</el-button>
      </div>
    </el-dialog> -->
  </div>
</template>
<script>
import { mapState } from 'vuex'

export default {
  data() {
    return {
      is_authority: {
        is_user_del: false,
        is_user_upt: false,
        is_user_add: false,
        is_user_add_role: false
      },
      // 非单个禁用
      single: true,
      // 非多个禁用
      multiple: true,
      uservo: {
        jxyxUser: { username: '' },
        page: {
          current: 1,
          size: 5
        }
      },
      dialogAddRole: false,
      usernames:[],
      ids: [],
      page: {
        current: 1,
        size: 5,
        total: 0
      },
      userList: [],
      role_list: [],
      //控制对话框
      dialogAddUser: false,
      passEditUser: true,
      // 添加用户
      addUserForm: {},
      addUserRules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 5, max: 15, message: '长度必须在5到15位之间', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 5, max: 15, message: '长度必须在5到15位之间', trigger: 'blur' }
        ],
      },
      is_authority: {
        is_user_del: false,
        is_user_upt: false,
        is_user_add: false,
        is_user_add_role: false
      }
    }
  },
  created() {
    this.isAuthority()
    this.queryPage()
  },
  computed: {
    ...mapState(['btus'])
  },
  methods: {
    
    isAuthority() {
      var arr = this.btus.split(',')
      for (var key in this.is_authority) {
        if (arr.indexOf(key) != -1) {
          this.is_authority[key] = true
        }
      }
    },
    handleImport() {
      this.upload.title = "用户导入";
      this.upload.open = true;
    },

    async queryPage() {
      const { data: res } = await this.$http.post(
        '/jxyxUser/queryPage',
        this.uservo
      )

      if (res.code != 20000) return this.$message.error(res.message)
      this.userList = res.data.list.records
      this.page.total = res.data.list.total
      this.page.current = res.data.list.current
      this.page.size = res.data.list.size
      console.log(this.userList)
    },
    // 多选框选中数据
    handleSelectionChange(selection) {
      this.usernames = selection.map(item => item.username)
      this.ids = selection.map(item => item.id)
      this.single = selection.length != 1
      this.multiple = !selection.length
    },
    // 监听每页展示的条数
    handleSizeChange(newSize) {
      this.uservo.page.size = newSize
      this.queryPage()
    },
    //监听页码
    handleCurrentChange(newCurrent) {
      this.uservo.page.current = newCurrent
      this.queryPage()
    },
    //更新状态
    async uptStatus(status) {
      const { data: res } = await this.$http.put(
        `/jxyxUser/users/${status.id}/state/${status.mgState}`
      )
      if (res.code != 20000) {
        status.mgState = !status.mgState
        return this.$message.error(res.message)
      }
      return this.$message.success(res.message)
    },
    //分配角色初始化数据
    async fprole(status) {
      if (status.roleId != undefined) {
        this.addUserForm.roleId = status.roleId
      }
      const { data: res } = await this.$http.get('/jxyxSysRole/queryAll')

      this.dialogAddRole = true
      if (res.code != 20000) {
        return this.$message.error(res.message)
      }
      this.role_list = res.data.data
      this.addUserForm.id = status.id
    },
    // 添加角色
    async addRole() {
      const { data: res } = await this.$http.post(
        '/jxyxUser/save',
        this.addUserForm
      )
      if (res.code != 20000) return this.$message.error(res.message)
      this.dialogAddRole = false
      this.queryPage()
      return this.$message.success(res.message)
    },
    //删除
    async del(row) {
      const username1 = this.usernames || row.username
      const userid = row.id || this.ids
      console.log(userid);
      this.$confirm('确认要删除用户名为 '+ username1 +' 的数据项吗?', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          const { data: res } = this.$http.delete(
            '/jxyxUser/del' + '/' + userid
          )
          // console.log(res);
          this.queryPage()
          this.userList = []
        })
        .then(() => {
          this.$message({
            showClose: true,
            message: '删除成功',
            type: 'success'
          })
        })
    },
    //关闭添加页面时初始化
    closeInit() {
      this.addUserForm = {}
      this.$refs.addFormRef.resetFields()
      this.passEditUser = false
    },
    closeAddRole() {
      this.dialogAddRole = false
      this.addUserForm = {}
    },
    //添加用户
    addUser() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post(
          '/jxyxUser/save',
          this.addUserForm
        )
        if (res.code != 20000) return this.$message.error(res.message)
        this.dialogAddUser = false
        this.passEditUser = false
        this.queryPage()
        return this.$message.success(res.message)
      })
    },
    
    //编辑用户
    async editUser(status) {
      this.passEditUser = false
      this.dialogAddUser = true
      const id = status.id || this.ids
      const { data: res } = await this.$http.get('/jxyxUser/getById' + '/' + id)
      if (res.code != 20000) return this.$message.error(res.message)
      this.addUserForm = res.data.data
    }
  },
  
}
</script>
<style lang="less" scoped>
.el-table {
  margin-top: 15px;
  font-size: 12px;
}
.el-row {
  display: flex;
  align-items: center;
}
.el-pagination {
  margin-top: 15px;
}
</style>
